/* HomePage.css */
.home-page {
  min-height: 100vh;
  background-color: #f5f5f5;
}

.hero-section {
  background: linear-gradient(135deg, #1a237e 0%, #283593 100%);
  color: white;
  padding: 80px 20px;
  text-align: center;
}

.hero-title {
  font-size: 3.5rem;
  font-weight: 700;
  margin-bottom: 20px;
  animation: fadeInUp 1s ease-out;
}

.hero-subtitle {
  font-size: 1.5rem;
  margin-bottom: 40px;
  opacity: 0.9;
  animation: fadeInUp 1s ease-out 0.2s;
}

.hero-buttons {
  display: flex;
  justify-content: center;
  gap: 20px;
  animation: fadeInUp 1s ease-out 0.4s;
}

.services-section {
  padding: 80px 20px;
  background-color: white;
}

.services-title {
  text-align: center;
  font-size: 2.5rem;
  font-weight: 600;
  color: #1a237e;
  margin-bottom: 50px;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Responsive Design */
@media (max-width: 768px) {
  .hero-title {
    font-size: 2.5rem;
  }
  
  .hero-subtitle {
    font-size: 1.2rem;
  }
  
  .hero-buttons {
    flex-direction: column;
    align-items: center;
  }
  
  .hero-buttons button {
    width: 200px;
  }
}